/* Login iFrame */

import getAuthorizationLink from './authorize';
import $ from 'jQuery';
import getOrigin from './origin';
import CustomStorage from './CustomStorage';
import handleAuthorizationCallback from './callback';

const initLogin = (function () {

  let loginInit = false;

  return function (config, onToken, onError, params) {
    if (loginInit) return console.log('MDOIDC.initLogin: Login already initialized!');
    loginInit = true;
    let sourceOrigin = getOrigin(config.frameRedirectUrl);
    let authorizationStorage = new CustomStorage();

    function receiveCallbackEvent(event) {

      if (event.origin !== sourceOrigin) {
        console.log('MDOIDC.receiveCallbackEvent: Received message from wrong origin, ignoring', event.origin);
        return false;
      }

      if (!event.data || (typeof (event.data) === 'string' && !event.data.startsWith('?'))) {
        // not my business...
        return false;
      }

      console.log('MDOIDC.receiveCallbackEvent: Event from softlogin iframe', event);
      // Clean up
      if (params && params.frameId) {
        $('#' + params.frameId).hide();
      } else {
        $('#login-iframe').remove();
      }
      window.removeEventListener('message', receiveCallbackEvent, true);
      loginInit = false;
      // if we have a token, init site
      if (event.data.includes('code')) {
        console.log('MDOIDC.receiveCallbackEvent: Found code response, trying to get token.');
        return handleAuthorizationCallback(config, event.data, authorizationStorage)
          .then(onToken)
          .catch((err) => {
            console.log('MDOIDC.receiveCallbackEvent: Could not get token, silent quit.', err);
            onError && onError(err);
          });
      }
      onError && onError('unknown response');
      return console.log('MDOIDC.receiveCallbackEvent: No code in response, silent quit.');
    }

    // make sure postMessage is catched
    window.addEventListener('message', receiveCallbackEvent, true);
    if (params && params.frameId) {
      let query = params.query || {};

      query['redirect_uri'] = query['redirect_uri'] || config.frameRedirectUrl;
      let authZ = getAuthorizationLink(query, authorizationStorage);

      $('#' + params.frameId).prop('src', authZ).show();
    } else {
      // assume softlogin
      // create the authorization request for softlogin
      let authZ = getAuthorizationLink({
        'prompt': 'none',
        'redirect_uri': config.frameRedirectUrl}, authorizationStorage);

      $('body').append(`<iframe id='login-iframe' src='${authZ}' style='display: none' />`);
    }
    return true;
  };
}());

export default initLogin;
