/* Constructs the authorization link */
import uuidv4 from './uuidv4';
import generateCodeVerifier from './verifier';
import generatePkceChallenge from './challenge';

/* Constructs the authorization link */
function getAuthorizationLink(config, params, authorizeParamStorage) {

  // custom storage, used for iFramed flows
  authorizeParamStorage = authorizeParamStorage || sessionStorage;

  if (sessionStorage.getItem('state')) {
    console.warn('MDOIDC.getAuthorizationLink: Detected state in sessionStorage, may cause trouble.');
  }

  params = params || {};

  // defaults
  params['client_id'] = config.clientId;
  params['response_type'] = 'code';

  // Generate a unique state verifier
  params['state'] = uuidv4();
  console.log('MDOIDC.getAuthorizationLink: Created new state:', params.state);

  // Generate a unique verifier
  let verifier = generateCodeVerifier(128);

  console.log('MDOIDC.getAuthorizationLink: Created new verifier', verifier);

  // store values for use in callback.html
  authorizeParamStorage.setItem('code_verifier', verifier);
  authorizeParamStorage.setItem('state', params.state);

  // generate a pkce challenge from the verifier
  params['code_challenge_method'] = 'S256';
  params['code_challenge'] = generatePkceChallenge('S256', verifier);
  console.log('MDOIDC.getAuthorizationLink: Generated challenge', params.code_challenge);

  // ensure a redirect uri is set
  params['redirect_uri'] = params.redirect_uri || config.redirectUri;

  // remember used redirect uri for token request
  authorizeParamStorage.setItem('redirect_uri', params.redirect_uri);

  // construct the authorize url
  let authorize = `${config.oidcBaseUrl}/authorize?`;

  for (let key in params) {
    if (params.hasOwnProperty(key)) {
      authorize += `&${key}=${encodeURIComponent(params[key])}`;
    }
  }

  console.log('MDOIDC.getAuthorizationLink: Generated authorize url', authorize);

  return authorize;

}

export default getAuthorizationLink;
